    grid-area: pict-wrapper;
    height: 20vw;
    width: 30vh;
  }
  
  .pict-wrapper img {
    position: relative;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-attachment: fixed;
    background-size: cover;
  }
  

  
  
